@import url(https://fonts.googleapis.com/css?family=Roboto);
@import url(https://fonts.googleapis.com/css?family=Hind+Guntur);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-image: url(/static/media/background-2.860608d7.jpg);
  background-size: cover;
}

#root {
  background-color: rgba(248, 248, 248, 0.24);
}

.PlayerStats {
  margin: 0 auto;
  width: -webkit-min-content;
  width: -moz-min-content;
  width: min-content;
  background-color: white;
  min-height: 100vh;
}

.Dialog {
  position: absolute;
  left: calc(50% - 25em);
  top: calc(50% - 25em);
  width: 50em;
  height: auto;
  margin: 0 auto;
  background-color: white;
  box-shadow: 0 0 2px 0;
  padding: 2rem 0;
  z-index: 1;
}

.Dialog.Stats {
  display: grid;
  -webkit-align-items: center;
          align-items: center;
  -webkit-align-content: space-around;
          align-content: space-around;
  grid-template-columns: 1fr 4fr;
  grid-column-gap: 2em;
  grid-template-areas: 
    "i s"
    "n s";
}

.Dialog.About {
  padding: 2rem 2rem;
}

.Dialog h2 {
  text-align: center;
}

header {
  grid-area: n;
}

.DeepStats {
  grid-area: s;
}

.UserPicture {
  grid-area: i;
  width: 6em;
  height: 6em;
  justify-self: center;
}

.DeepStats img {
  max-height: 2.5rem;
}

.DeepStats .ImageWrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  max-width: 2.5rem;
  margin: 0.5rem;
}

.UserPicture img {
  object-fit: fill;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

.ModalBlock {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
}
